@font-face {
  font-family: "ptmono";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptmono-regular.woff2") format("woff2"), url("../fonts/ptmono-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "robotocondensed";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/robotocondensed-regular.woff2") format("woff2"), url("../fonts/robotocondensed-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "robotocondensed";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/robotocondensed-bold.woff2") format("woff2"), url("../fonts/robotocondensed-bold.woff") format("woff");
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "ptmono", "Courier", monospace;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  min-width: 320px;
  background-color: #ffffff;
  color: #666666;
}

header {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: flex-start;
  width: 100%;
  background-color: transparent;
}
@media (min-width: 1024px) {
  header {
    padding: calc((100% - 73.4895%) / 2);
    padding-top: 33px;
    padding-bottom: 10px;
  }
}
@media (max-width: 1023px) {
  header {
    padding: calc((100% - 93.5065%) / 2);
    padding-top: 20px;
    padding-bottom: 10px;
  }
}
@media (max-width: 767px) {
  header {
    padding: calc((100% - 90.625%) / 2);
    padding-top: 17px;
    padding-bottom: 10px;
  }
}

main {
  height: 100px;
  background-color: #000;
}

footer {
  outline: 1px dashed red;
  outline-offset: -1px;
}

img {
  display: block;
}

.container {
  margin-right: auto;
  margin-left: auto;
  max-width: 1920px;
}

.visually-hidden {
  position: absolute;
  clip: rect(0, 0, 0, 0);
  clip-path: inset(100%);
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  overflow: hidden;
}

.modal-open {
  overflow: hidden;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.button {
  border: none;
  outline: none;
}

.link {
  display: block;
  position: relative;
  text-decoration: none;
  color: inherit;
}

.link,
.button {
  cursor: pointer;
}

.logo {
  padding-right: 100px;
  margin-right: auto;
}
@media (max-width: 767px) {
  .logo img {
    width: 38px;
    height: 38px;
  }
}
.logo--footer {
  box-sizing: content-box;
  width: 216px;
}
@media (min-width: 1024px) {
  .logo--footer {
    margin-bottom: 35px;
    padding-right: 20px;
  }
}

@media (min-width: 1024px) {
  .contacts {
    margin-right: 2.7%;
    padding-right: 10px;
    color: #ffffff;
  }
}

.contacts__list {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  width: max-content;
}
@media (min-width: 1024px) {
  .contacts__list {
    padding-top: 12px;
  }
}

.contacts__item {
  margin-bottom: 15px;
}
@media (min-width: 1024px) {
  .contacts__item:not(:last-child) {
    margin-right: 88px;
  }
}
@media (max-width: 1023px) {
  .contacts__item:not(:last-child) {
    margin-right: 33px;
  }
}
@media (max-width: 767px) {
  .contacts__item:not(:last-child) {
    margin-right: 23px;
  }
}

@media (min-width: 1024px) {
  .contacts__link {
    font-family: "ptmono", "Courier", monospace;
    font-size: 16px;
    line-height: 18px;
    font-weight: 400;
    color: inherit;
  }
}
@media (min-width: 1024px) {
  .contacts__link--mail {
    padding-top: 4px;
  }
}
@media (max-width: 1023px) {
  .contacts__link span {
    position: absolute;
    clip: rect(0, 0, 0, 0);
    clip-path: inset(100%);
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    overflow: hidden;
  }
}
@media (min-width: 1024px) {
  .contacts__link:hover span, .contacts__link:focus span {
    text-decoration: underline;
  }
}
.contacts__link:hover .contacts__link-svg, .contacts__link:focus .contacts__link-svg {
  --icon-color: #ff4d00;
}
.contacts__link:active {
  opacity: 0.7;
}

.contacts__link-svg {
  --icon-color: #00b2ff;
  fill: --icon-color;
}
@media (min-width: 1024px) {
  .contacts__link-svg {
    margin-right: 10px;
  }
}

@media (min-width: 1024px) {
  .contacts__phone-call-svg {
    margin-right: 5px;
    transform: translateY(4px);
  }
}
@media (max-width: 1023px) {
  .contacts__phone-call-svg {
    width: 24px;
    height: 24px;
    transform: translateY(12px);
  }
}
@media (max-width: 767px) {
  .contacts__phone-call-svg {
    transform: translateY(5px);
  }
}

@media (min-width: 1024px) {
  .contacts__envelope-svg {
    transform: translateY(3px);
  }
}
@media (max-width: 1023px) {
  .contacts__envelope-svg {
    width: 25.33px;
    height: 18.67px;
    transform: translateY(15px);
  }
}
@media (max-width: 767px) {
  .contacts__envelope-svg {
    transform: translateY(8px);
  }
}

.ring-trigger {
  font-family: "robotocondensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: #ffffff;
  margin-top: 3px;
  padding: 15px 18px 8px 17px;
  border: 1px solid #ffffff;
  border-radius: 5px;
}
@media (min-width: 1024px) {
  .ring-trigger:hover, .ring-trigger:focus {
    color: #ff4d00;
    border-color: currentColor;
  }
}
.ring-trigger:active {
  opacity: 0.7;
}

@media (min-width: 1024px) {
  .service-information__box {
    padding: calc((100% - 73.5417%) / 2);
  }
}
@media (max-width: 1023px) {
  .service-information__box {
    padding: calc((100% - 93.5065%) / 2);
  }
}
@media (max-width: 767px) {
  .service-information__box {
    padding: calc((100% - 100%) / 2);
    display: flex;
  }
}
.service-information__box--upper {
  display: grid;
}
@media (min-width: 1024px) {
  .service-information__box--upper {
    grid-template-columns: min-content 1fr;
    grid-template-rows: repeat(2, min-content);
    column-gap: 13.1%;
    padding-top: 65px;
    padding-bottom: 55px;
    border-bottom: 1px solid rgba(98, 105, 98, 0.2);
  }
}
@media (max-width: 1023px) {
  .service-information__box--upper {
    padding-top: 19px;
    padding-bottom: 10px;
  }
}
@media (max-width: 767px) {
  .service-information__box--upper {
    padding-top: 46px;
    padding-bottom: 10px;
  }
}
.service-information__box--lower {
  font-size: 13px;
  line-height: 24px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #1b2d37;
  display: flex;
  align-items: center;
}
@media (min-width: 1024px) {
  .service-information__box--lower {
    padding-top: 3px;
    padding-bottom: 13px;
  }
}
@media (max-width: 1023px) {
  .service-information__box--lower {
    padding-top: 21px;
    padding-bottom: 10px;
  }
}
@media (max-width: 767px) {
  .service-information__box--lower {
    padding-top: 28px;
    padding-bottom: 10px;
  }
}
.service-information__box--lower p {
  margin-top: 0;
  margin-bottom: 0;
  opacity: 0.5;
}
.service-information__box--lower a {
  opacity: 0.5;
}
.service-information__box--lower a:hover, .service-information__box--lower a:focus {
  text-decoration: underline;
}
.service-information__box--lower a:active {
  text-decoration: none;
  opacity: 1;
}

@media (min-width: 1024px) {
  .service-information__logo {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 1;
    grid-row-end: 2;
  }
}

@media (min-width: 1024px) {
  .service-information__socials {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 2;
    grid-row-end: 3;
  }
}

@media (min-width: 1024px) {
  .service-information__accordeon {
    grid-column-start: 2;
    grid-column-end: -1;
    grid-row-start: 1;
    grid-row-end: 3;
  }
}

.service-information__rights {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  width: 36.5%;
  margin-right: 5.8%;
}
.service-information__rights p {
  flex-shrink: 0;
}
@media (min-width: 1024px) {
  .service-information__rights p:first-child {
    margin-right: 13.5%;
  }
}
@media (min-width: 1024px) {
  .service-information__rights p:last-child {
    flex-grow: 1;
  }
}

@media (min-width: 1024px) {
  .service-information__privacy-terms-link {
    margin-right: 28%;
  }
}

@media (min-width: 1024px) {
  .service-information__developed-by {
    transform: translateX(10px);
  }
}

.socials {
  max-width: 216px;
}
@media (min-width: 1024px) {
  .socials {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
}

.socials__item {
  margin-bottom: 10px;
}
@media (min-width: 1024px) {
  .socials__item:not(:last-child) {
    margin-right: 15.54px;
  }
}
.socials__item:hover .socials__svg, .socials__item:focus .socials__svg {
  --icon-color: #00b2ff;
}
.socials__item:active .socials__svg {
  opacity: 0.5;
}

.socials__svg {
  --icon-color: #cdcdcd;
  fill: --icon-color;
}

.accordeon {
  font-family: "ptmono", "Courier", monospace;
  font-size: 15px;
  line-height: 25px;
  font-weight: 400;
  color: #1b2d37;
  display: grid;
  grid-template-columns: 1fr max-content;
  grid-template-rows: repeat(2, min-content);
  grid-auto-flow: column;
}
@media (min-width: 1024px) {
  .accordeon {
    column-gap: 10%;
  }
}
.accordeon a {
  opacity: 0.5;
}
.accordeon a:hover, .accordeon a:focus {
  text-decoration: underline;
}
.accordeon a:active {
  text-decoration: none;
  opacity: 1;
}

.accordeon__btn {
  font-family: "robotocondensed", "Arial", sans-serif;
  font-size: 15px;
  line-height: 16px;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: -0.03em;
  color: inherit;
  width: max-content;
  margin-bottom: 25px;
  padding: 0;
  background-color: transparent;
  cursor: auto;
}

.accordeon__site-map {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
}

@media (min-width: 1024px) {
  .accordeon__list-item {
    margin-bottom: 15px;
  }
}
@media (min-width: 1024px) {
  .accordeon__list-item:nth-child(2n+1) {
    margin-right: 13.2%;
  }
}

.accordeon__map-item--accessories, .accordeon__map-item--delivery, .accordeon__map-item--contacts {
  line-height: 26px;
  letter-spacing: 0.03em;
}

.accordeon__address {
  font-style: normal;
}

.accordeon__location {
  opacity: 0.5;
}

.hidden-entity {
  display: none;
}

@media (max-width: 1023px) {
  .hidden-before-desktop {
    display: none;
  }
}

@media (min-width: 1024px) {
  .hidden-on-desktop {
    display: none;
  }
}